import React, { useState, useEffect } from 'react';
import { Link, Redirect } from 'react-router-dom';
import { useHistory } from "react-router-dom";
import M from 'materialize-css';
const axios = require('axios');

const api = process.env.REACT_APP_API

function Voting(props) {
  const votes = props.votes;
  const [vote, voteSet] = useState('');

  const castVote = n => {
    if(n !== vote){
      props.castVote(n);
      voteSet(n);
    } else{
      console.log('vote already added on this player', n)
    }
  }

  return (
    <div className="container">
      <div className="section"></div>
      {props.admin ?
        <div className="row">
          <div className="col s12 center-align">
            <a className="waves-light btn grey darken-1" onClick={props.endGame}>End Game</a>
          </div>
        </div>
      : null}
      <div className="row">
        {Object.keys(votes).map((name, i) =>
          <div className="col s12" key={i}>
            <div className="col s1 m3"></div>
            <div className="col s3 m2">
              <h4>{name}</h4>
            </div>
            <div className="col s4 m2 right-align">
              <h4>{votes[name].length}</h4>
            </div>
            <div className="col s3 m2 right-align" style={{marginTop: '25px'}}>
              <a className="btn-small grey darken-1" onClick={() => castVote(name)}>Vote</a>
            </div>
            <div className="col s1 m3"></div>
          </div>
        )}
      </div>
    </div>
  );
}

export default Voting;
